<template>
    <a-layout style="height: 100vh;">
      <a-layout-header>
        <a-space style="margin-left: 5%;">
          <template #split><a-divider direction="vertical" /></template>
          <a-link @click="this.$router.back">{{ "< 返回" }}</a-link>
          <h1  style="font-size: 25px;">课程详情</h1>
          <a-button type="primary" :loading="get_all_loading" @click="reload"
          style="float: right;">
            {{ get_all_loading ? "加载中" : "重新加载" }}
          </a-button>
        </a-space>
      </a-layout-header>
  
      <a-layout-content>
        <a-table :columns="columns" :data="course_detail" :scroll="{ x:600,y: 1200 }"
         column-resizable :bordered="{cell:true}"
        style="width: 60%;left: 5%;">
        </a-table>
      </a-layout-content>
    </a-layout>
  </template>
    
  <script>
  import teacher_api from "@/api/teacher/teacher-api"
  
  export default {
    data() {
      return {
        columns: [
          { title: '序号', dataIndex: 'index' },
          { title: '日期', dataIndex: 'date' },
          { title: '节次', dataIndex: 'lesson' },
          { title: '教室', dataIndex: 'classroom' },
        ],
        course_detail: [],
        course_id: "",
        get_all_loading: false
      }
    },
  
    mounted() {
      this.course_id = this.$route.query
      // console.log(this.course_id)
      this.get_courses_detail();
    },
  
    methods: {
      reload(){
        this.get_courses_detail();
      },
      
      set_get_all_btn_loading(is_loading) {
        this.get_all_loading = is_loading
      },

      get_courses_detail() {
        this.set_get_all_btn_loading(true)
        teacher_api.get_courses_detail({ course_id: this.course_id })
          .then(resp => {
            this.set_get_all_btn_loading(false)
            if (resp.status_code != 200) {
              this.$message.error(resp.status_text)
              return
            }
  
            var cdata = []
            var tdata = []
            cdata = resp.data
            for (let index = 0; index < cdata.length; index++) {
              tdata.push({
                index: 0,
                date: cdata[index].date,
                lesson: cdata[index].lesson,
                classroom: cdata[index].classroom,
              })
            }

            tdata.sort(function(a, b) {
              // 将日期字符串转换为可以比较的值
              const dateA = new Date(a.date)
              const dateB = new Date(b.date)
              // console.log(a.date+"-"+a.date+" = "+String(dateA - dateB))
              const d = dateA - dateB
              if(dateA - dateB ==0 ){
                return parseInt(a.lesson)-parseInt(b.lesson)
              }
              // 比较日期对象
              return dateA - dateB;
            });

            for (let index = 0; index < cdata.length; index++) {
              tdata[index].index=index+1
            }

            this.course_detail = tdata
            console.log(tdata)
          })
          .catch(err => {
            this.set_get_all_btn_loading(false)
            this.$message.error("加载失败")
            console.error(err)
          })
      }
    }
  }
  </script>
    